.itemMovie {
  width: 451px;
  height: 257px;
  flex-shrink: 0;
  background-color: #fff;
  margin: 10px;
  border-radius: 0;
  position: relative;

  /* @shadow-2 */
  box-shadow: 0px 4px 12px 0px rgba(0, 0, 0, 0.15);
}
.textItemMovie {
  color: black;
}
.itemMovie-img {
  display: block;
  width: 170px;
  height: 256px;
  flex-shrink: 0;
}

.itemMovie-text {
  text-align: left;
  padding: 10px 0px;
}

.genreButton {
  color: rgba(0, 0, 0, 0.65);
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  width: auto;
  height: 20px;
  flex-shrink: 0;
  border-radius: 2px;
  border: 1px solid #d9d9d9;
  background: #fafafa;
  padding: 0px 2px;
}

.itemMovie-date {
  padding-bottom: 5px;
}
.example {
  margin: 20px 0;
  margin-bottom: 20px;
  padding: 30px 50px;
  text-align: center;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
}
.title {
  text-align: left;
}

@media (min-width: 320px) and (max-width: 768px) {
  .itemMovie {
    width: 320px;
    height: 205px;
  }

  .itemMovie-img {
    width: 60px;
    height: auto;
    padding-top: 5px;
    padding-left: 5px;
  }

  .itemMovie-text {
    position: absolute;
    bottom: 40px;
    left: 10px;
  }
  .starRating {
    position: absolute;
    bottom: 5px;
  }
}
